*,
::after,
::before {
 box-sizing:unset !important;
}
body {
    line-height:unset !important;
}
.board {
    font-size: unset;
    font-weight: unset;    
    text-align: left;

    width: 225px;
    height: 225px;
}
.avail {
    font-size: smaller;    
}
.availnums{
    font-size: small;
    font-weight: bold;
}
.none{
    display: none !important;
}
.subset2{
    background-color : yellow;
    font-size: .5rem;
}
.subset3{
    background-color : gray;
    font-size: .5rem;
}
.unique{
    background-color : orange;
}
.single{
    background-color : green;
}
.selected{
    background-color : blue;
}
.hiddensubset2{
    background-color: cornflowerblue;
    font-size: .5rem;
}
.hiddensubset3{
    background-color: rgb(237, 100, 207);
    font-size: .5rem;
}
.removedNumber{
    color: red;
}
.highlightBtns{
    font-size: .75rem !important;
    padding: 3px !important;
    margin: 2px !important;
    
}